import React from 'react';
import { MDBRow, MDBCol} from "mdb-react-ui-kit";
import './footermotokey.css';
import { keyShop } from "../Db";


export default function Footermotokey() {
  return (
    <div className='footer-motokey'>
        <div className='footer-keymoto'>

        <h2>Cn dch v lm cha kha xe my b mt ca bn  tp Bun Ma Thut?</h2>
        <p>Lm cha kha xe my c th l mt thch thc, nhiu th kha khng c kin thc v s trnh nhng cng vic nh vy. Chng ti  {keyShop.title} s khng lm bn tht vng, cc k thut vin ca chng ti mang theo nhng cng c tin tin nht  c th lm mi cha kha xe my mi nht c th. V vy, nu bn ang tm kim mt th kha xe my c trnh   tp Bun Ma Thut m bn c th tin tng, hy gi cho chng ti ngay hm nay {keyShop.phone} hoc lin h vi chng ti  t lch hn. </p>
        </div>
        <div className='footer-car'>
        <MDBRow>
        <MDBCol md="5">
          <h2>Lin H Vi Chng Ti</h2>
          <p>Bn c cu hi? Lin h vi Chng ti  c gii p.</p>
          <h3>Thi gian lm vic:</h3>
          <p>Th Hai - Ch Nht: 7:00 am- 6:00 pm</p>
          <p>Dch v khn cp 24/7.</p>
        </MDBCol>
        <MDBCol md="1">
        <a href= {keyShop.facebook}>
        <img
            src="../assets/services/carkey/facebook.png"
            alt=""
            className="image-face"
          />
          </a>
          <h5>Fanpage</h5>
        </MDBCol>
        <MDBCol md="1">
        <img
            src="../assets/services/carkey/phone_icon-1920w.png"
            alt=""
            className="image-phone"
          />
          <h5>Phone:</h5>
          <h5> {keyShop.phone}</h5>
        </MDBCol>
        <MDBCol md="5">
        <img
            src="../assets/services/carkey/key_man_red-1920w.png"
            alt=""
            className="image-man"
          />
        </MDBCol>
        </MDBRow>
            
        </div>

    </div>
  )
}
